.continue {
  cursor: pointer;
  font-weight: 700;
  font-family: Helvetica,"sans-serif";
  transition: all .2s;
  padding: 10px 20px;
  border-radius: 100px;
  background: white;
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  font-size: 15px;
}

.continue:hover {
  background: white;
}

.continue > svg {
  width: 34px;
  margin-left: 10px;
  transition: transform .3s ease-in-out;
}

.continue:hover svg {
  transform: translateX(5px);
}

.continue:active {
  transform: scale(0.95);
}

